@import url(https://fonts.googleapis.com/css2?family=Oxygen:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: 'Oxygen', sans-serif !important;
  background-color: var(--bg-color);
  color: var(--font-color);
  font-weight: 400;
  font-size: 15px;
  line-height: 1.8;
}
a, a:hover, a:focus, a:active {
  text-decoration: none !important;
  color: inherit !important;
}

#lara-page {
  width: 100%;
  overflow: hidden;
  position: relative;
  background-color: #000;
}

.container-wrap {
  max-width: 1170px;
  margin: 0 auto;
}
.sub-heading{
  text-transform: uppercase;  
  font-size: 2.5rem;
}


/* * * * * * * * * * * * * * * * * * * * * * * N A V   B A R * * * * * * * * * * * * * * * * * * * * * * * * * * * */

#lara-aside {
  font-family: 'Oxygen', sans-serif !important;
  padding-bottom: 40px;
  float: left;
  width: 300px;
  position: fixed;
  overflow-y: inherit;
  z-index: 1001;
  color: #e1e1ff;
  height: 900px !important;
}

@media (max-width: 768px){
  #lara-aside {
    background-color: var(--bg-color);
    color: var(--font-color);
    width: 300px;
    padding-top: 2em;
    color: #e1e1ff;
}
}

#lara-aside .position {
  display: block;
  margin-bottom: 2em;
  font-size: 12px;
  text-transform: uppercase;
  color: #e1e1ff !important;
  text-decoration: none;
  color: inherit;
}

#navbarNav{
  transition: 0.5s;
  overflow-y: inherit;
  z-index: 1001;
  background-color: rgb(0 0 0);
  height: 900px !important;
}


/*** toggler ***/
.lara-nav-toggle {
  position: fixed;
  left: -5px;
  top: 0px;
  z-index: 9999;
  cursor: pointer;
  opacity: 1;
  padding: 20px;
  visibility: hidden;
}

.navbar-toggler:focus {
  text-decoration: none;
  outline: 0;
   box-shadow: 0 0 0 0rem; 
}
@media screen and (max-width: 767px){
  .lara-nav-toggle {
    opacity: 1;
    visibility: visible;
}

}

.lara-nav-toggle:hover,
.lara-nav-toggle:focus,
.lara-nav-toggle:active {
  outline: none;
  border-bottom: none !important;
}



#lara-aside  ul  li.active a {
  color: #ad91c0 !important;
}

#lara-aside  ul li a:hover,
#lara-aside  ul li a:active,
#lara-aside  ul li a:focus {
  text-decoration: none;
  outline: none;
  color: #96bfe0 !important;
  -webkit-animation: pulse;
          animation: pulse; /* referring directly to the animation's @keyframe declaration */
  -webkit-animation-duration: 2s;
          animation-duration: 2s; /* don't forget to set a duration! */
}


.lara-icons {
  padding: 4px;
  width: 2.75rem;
  height: 2em;
  margin-left: 10px;
}
/*** navbar image ***/
.profile-img {
  height: 150px;
  width: 150px;
  margin: 0 auto;
  margin-bottom: 30px;
}

.photo-wrap {
  padding-top: 3rem;
}

/*** h1 navbar name ***/

.Lara-Name{
  font-weight: 500;
  font-size: 3rem;
  margin-bottom: 0.5em;
  display: block;
  width: 100%;
}

/*** navbar menu items ***/
#lara-aside #lara-main-menu ul {
  text-align: center;
  margin: 0;
  padding: 0;
}

#lara-aside #lara-main-menu ul li {
  font-family: 'Oxygen', sans-serif !important;
  margin: 0 0 2rem 0;
  padding: 0;
  list-style: none;
  font-size: 1.25rem;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 1px;
}

#lara-aside #lara-main-menu ul li a {
  color: var(--font-color);
  text-decoration: none;
  position: relative;
  padding: 10px 0;
}


#lara-aside #lara-main-menu {
  display: block;
  width: 100%;
}

#lara-aside #lara-main-menu .collapse {
  display: block !important;
}

svg:not(:root).svg-inline--fa {
  overflow: visible;
  color: rgb(99, 92, 83) !important;
}


.fcons {
  padding: 4px;
  width: 2.75rem;
  height: 2.75rem;
  margin-left: 10px;
}

.copy-rights {
  padding-top: 2rem;
  color: rgba(146, 129, 136, 0.877);
  -webkit-align-content: center;
          align-content: center;
  text-align: center !important;

}


/* * * * * * * * * * * * * * * * * * * * * * * M A I N * * * * * * * * * * * * * * * * * * * * * * * * * * * */


#lara-main {
  width: calc(100% - 300px);
  float: right;
  transition: 0.5s;
}

@media(max-width: 767px){
  #lara-main {
    width: 100%;
    padding: 0 1em;
}
}

@media screen and (max-width: 768px){
.lara-contact {
    padding-top: 5em;
    padding-bottom: 8em;
  }
}


